<template>
	<div v-show="isActive"><slot></slot></div>
</template>

<script>
export default {
	name: 'tab',
  	props : {
		name : {
			required : true
		},
		selected : {
			default : false
		}
	},

	data() {
		return { 
			isActive : false
		};
	},

	computed : {
		href() {
			//about our culture
			return '#' + this.name.toLowerCase().replace(/ /g, '-');
		}
	},

	mounted() {
		this.isActive = this.selected;
	}
}
</script>


